@import '../../../base';

.default-flow-enquiry-modal {
  max-width: 600px !important;

  .modal-header {
    padding-left: 5px;
    padding-right: 5px;
    .modal-title {
      width: 100%;
      transform: translateX(40px);
      padding-right: 40px;
    }
  }

  .modal-body {
    padding-left: 40px;
    padding-right: 40px;    
  }

  ol {
    padding-left: 18px;
  }

  .desc-text {
    padding: 0 5px;
    font-weight: $font-weight-normal;
    font-size: $font-size-tiny;

    color: $color-input-text;

    margin-bottom: 12px;
  }

  .desc-heavy {
    font-weight: $font-weight-bold;
  }

  .modal-footer {
    padding-top: 0;
  }

  .button-container {
    display: flex;
    justify-content: center;
    margin: 5px 0;

    @media screen and (min-width: 576px) {
      &:nth-of-type(1) {
        padding-right: 5px;
        justify-content: flex-end;
      }
  
      &:nth-of-type(2) {
        padding-left: 5px;
        justify-content: flex-start;
      }
    }

    button {
      p {
        font-size: $font-size-smallest;
      }
    }

    .cancel-btn {
      border: 1px solid $color-input-form-landing-desc2;
      p {
        color: $color-input-form-landing-desc2;
      }

      &:hover {
        border: 1px solid $color-hover;
        p {
          color: $color-hover;
        }
      }
    }
  }
}