@import '../../../base';

.business-flow-container {
  background-color: $product-background;
  min-height: calc(100vh - 60px);
  @media screen and(min-width: 786px) {
    min-height: calc(100vh - 84px);
  }
  display: flex;
  flex-direction: column;
  overflow: auto;

  h3 {
    font-weight: $font-weight-bold;
    font-size: $font-size-smallest;
    color: $color-input-text;
    margin: 0;

    @media screen and (min-width: 992px) {
      font-size: $font-size-big;
    }
  }

  h6 {
    font-weight: $font-weight-normal;
    font-size: $font-size-tiny;
    color: $color-input-text;
    margin: 0;

    @media screen and (min-width: 992px) {
      font-weight: $font-weight-semibold;
    }
  }

  .business-flow-inner-container {
    flex: 1;
    margin: 0 auto;
    width: 1200px;
    max-width: 100%;
    padding: 0 15px;
    overflow: visible;
    min-height: calc(100vh - 120px - 221px);

    @media screen and (min-width: 768px) {
      min-height: calc(100vh - 100px - 221px);
    }

    .flow-container {
      margin: 40px 0;

      @media screen and (min-width: 768px) {
        margin: 40px 20px;
      }

      @media screen and (min-width: 1024px) {
        margin: 40px;
      }
    }

    .flow-back {
      display: flex;
      justify-content: flex-start;
      align-items: center;

      button {
        h6 {
          font-weight: $font-weight-bold;

          @media screen and (min-width: 992px) {
            font-weight: $font-weight-bold;
          }
        }
      }
    }

    .flow-title {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      position: relative;

      .title-img {
        position: absolute;
        transform: translateY(10px);
        @media screen and (max-width: 991px) {
          opacity: 0;
        }
      }

      h3 {
        z-index: 1;
      }
    }

    .flow-next {
      display: flex;
      justify-content: flex-end;
      align-items: center;

      button {
        transform: translateY(-5px);
        @media screen and (max-width: 991px) {
          min-width: 64px !important;
          min-height: 36px !important;

          p {
            font-size: $font-size-tiny;
          }
        }
      }
    }
  }

  .top-container {
    padding: 70px 0 10px 0;
    @media screen and (min-width: 992px) {
      padding: 50px 0 20px 0;
    }
  }

  .sticky-top-container {
    .flow-title {
      flex : 1;
      padding: 0;
    }

    h3 {
      flex: 1;
      max-width: 300px;

      @media screen and (min-width: 992px) {
        transform: translateY(-5px);
        font-size: $font-size-regular;
      }
    }
  }
}

.sticky-top-price {
  line-height: 1.8rem;
  margin-right: 40px;
  @media screen and (max-width: 991px) {
    display: none;
  }

  p:nth-of-type(1) {
    font-weight: $font-weight-semibold;
    font-size: $font-size-tiny;
    text-align: right;
    color: $color-input-text;
    margin: 0;
  }

  p:nth-of-type(2) {
    transform: translateY(-8px);
    font-weight: $font-weight-heavy;
    font-size: $font-size-big;
    text-align: right;
    color: $color-input-text;
    margin: 0;
  }
}

.business-step-progress {
  background-color: $product-background;
  .input-wrapper-main-new {
    background-color: $product-background;
  }

  .slider-container {
    width: 100vw;
    min-width: 100vw;
    transform: translateX(-15px);
  }
}
