@import '../../../base';

.check-out-complete-popup {
  max-width: 600px;
  .check-out-complete {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  
    p {
      position: relative;
      z-index: 1;
      padding: 0 10px;
    }
    img {
      max-width: 90%;
      opacity: 0;
  
      @media screen and (max-width: 576px) {
        max-width: 60%;
      }
    }
  
    // @keyframes bounceIn{
    //   0%{
    //     opacity: 0;
    //     transform: scale(0.3) translate3d(0,0,0);
    //   }
    //   50%{
    //     opacity: 0.9;
    //     transform: scale(1.1);
    //   }
    //   80%{
    //     opacity: 1;
    //     transform: scale(0.89);
    //   }
    //   100%{
    //     opacity: 1;
    //     transform: scale(1) translate3d(0,0,0);
    //   }
    // }
  
    // .bounce-in {
    //   opacity: 0;
    //   animation-name: bounceIn;
    //   animation-duration: 450ms;
    //   animation-timing-function: linear;
    //   animation-fill-mode: forwards;
    // }

    .bounce-in {
      animation: bounceIn 1s;
      animation-timing-function: linear;
      animation-fill-mode: forwards;
    }
  
    .upper-container {
      p,
      span {
        text-align: center;
        font-size: $font-size-big;
        font-weight: $font-weight-semibold;
        color: $color-title;
  
        @media screen and (max-width: 576px) {
          font-size: $font-size-regular;
        }
      }
  
      .focus-text {
        color: $color-hover;
      }
    }
  
    .upper-margin-up {
      margin-top: -50px;
    }
  
    .upper-margin-down {
      margin-top: 30px;
    }
  
    .extra-text,
    .mid-container {
      p,
      span {
        text-align: center;
        font-size: $font-size-small;
        font-weight: $font-weight-light;
        color: $color-caption;
        max-width: 350px;
  
        @media screen and (max-width: 576px) {
          font-size: $font-size-smallest;
        }
      }
    }
  
    .button-container {
      display: flex;
      margin-top: 27px;
  
      button {
        margin: 15px;
      }
  
      @media screen and (max-width: 576px) {
        margin-top: 10px;
        // flex-direction: column;
  
        button {
          min-width: 100px !important;
          min-height: 46px !important;
          p {
            font-size: $font-size-small !important;
          }
        }
      }
    }
  }
}
