@import '../../../../base';

.business-flow-container {
  .business-bottom-sticky-container {
    display: flex;

    h6 {
      font-weight: $font-weight-normal;
      font-size: $font-size-tiny;
      color: $color-input-text;
      margin: 0;

      @media screen and (min-width: 992px) {
        font-weight: $font-weight-semibold;
      }
    }

    .product-info {
      flex: 1;
    }

    .price-info {
      display: flex;
      align-items: center;
    }
  }
}