@import '../../../base';

.modal-popup-custom {
  max-width: 880px;

  .modal-content {
    padding-left: 35px;
    padding-right: 35px;
    padding-bottom: 30px;
    border-radius: 20px;
  }

  .modal-header {
    border-bottom: none;

    h3 {
      font-weight: $font-weight-bold;
      font-size: $font-size-big;

      color: $color-input-text;
    }

    .close {
      transform: translateX(20px);
      font-size: 32px;
    }
  }

  .modal-footer {
    border-top: none;
  }
}