.business-sticky {
  z-index: 1040;
  @media screen and (min-width: 992px) {
    z-index: 1030;
  }
  position: fixed;
  
  left: 0;
  right: 0;
  background: white;
  opacity: 0;

  transition: transform 0.75s ease, opacity 0.75s ease;

  &.show-top-sticky {
    transform: translateY(60px);
    opacity: 1;
    box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.05);

    @media screen and (min-width: 992px) {
      transform: translateY(100px);
    }
  }

  &.show-bottom-sticky {
    transform: translateY(-60px);
    opacity: 1;
    box-shadow: 0px -8px 10px rgba(0, 0, 0, 0.05);

    @media screen and (min-width: 992px) {
      transform: translateY(-88px);
    }
  }

  &.top-sticky {
    top: -60px;
    height: 60px;

    @media screen and (min-width: 992px) {
      top: -100px;
      height: 100px;
    }
  }

  &.bottom-sticky {
    bottom: -60px;
    height: 60px;

    @media screen and (min-width: 992px) {
      bottom: -88px;
    height: 88px;
    }
  }

  .sticky-inner {
    max-width: 1110px;
    margin: auto;
    padding: 15px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
